@keyframes button {
    from {}
    to {}
}

body {
    background-image: url('../img/background.jpeg');
    background-size: 100%;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;
    padding-bottom: 50px;
    background-color: #8f78bd;
    font-family: 'Muli', sans-serif;
}

a {
    text-decoration: none;
}

#bar::after {
    clear: both;
}

#bar {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow: hidden;
    text-align: center;
    background-color: #33235f;
    height: 70px;
    top: 0;
}

#nav::after{
    clear: left;
}

#nav {
    float: right;
    height: inherit;
    padding-right: 60px;
    vertical-align: middle;
}

#nav ul::after {
    clear: right;
}

#nav ul {
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: inherit;
}

#nav a {
    float: left;
    text-align: center;
    height: inherit;
    -webkit-transition: background-color 0.3s;
    -o-transition: background-color 0.3s;
    transition: background-color 0.3s;
}

#nav a:hover {
    cursor: pointer;
    background-color: #604c8d;
}

#nav a:hover li {
    color: white;
}

#nav li {
    height: 100%;
    width: 120px;
    margin: 0 auto;
    font-size: 17px;
    font-weight: 300;
    color: #d1c4e9;
    text-decoration: none;
    display: table;
}

#nav span {
    display: table-cell;
    vertical-align: middle;
}

#title {
    margin-left: 50px;
    padding: 10px;
    float: left;
}

#title h1 {
    margin: 0;
}

#title a {
    font-weight: 700;
    color: white;
    font-size: 40px;
    text-decoration:  none;
}

button {
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
    border: 0;
    border-radius: 3px;
    background-color: #604c8d;
    text-align: center;
    padding: 13px;
    width: 130px;
    font-weight: 300;
    color: #FFFFFF;
    font-size: 16px;
    -webkit-transition: -webkit-transform 0.1s;
    transition: -webkit-transform 0.1s;
    -o-transition: transform 0.1s;
    transition: transform 0.1s;
    transition: transform 0.1s, -webkit-transform 0.1s;
}

button:hover {
    -webkit-transform: translateY(-4px);
        -ms-transform: translateY(-4px);
            transform: translateY(-4px);
}

button:active {
    background-color: #33235f;
    -webkit-transform: translateY(0px);
        -ms-transform: translateY(0px);
            transform: translateY(0px);
}

#mb-container {
    height: 100%;
    width: 100%;
}

.middlebox {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 3px;
    margin: 0 auto;
    margin-top: 60px;
}

#middlebox-topbar {
    top: 0;
    border-radius: 3px 3px 0px 0px;
    vertical-align: top;
    width: 100%;
    overflow: hidden;
    background-color: #604c8d;
    padding: 10px;
}

#middlebox-topbar p{
    color: white;
    margin: 0;
    margin-left: 10px;
    vertical-align: middle;
    font-size: 20px;
}

@media screen and (max-width: 768px) {
    /* For mobile phones: */
    #title {
        text-align: center;
        width: 100%;
        margin-left: 0;
        padding-left: 0;
    }

    #nav {
        overflow: hidden;
        padding-right: 0;
        width: 100%;
    }

    #nav ul {
        width: 100%;
        clear: right;
    }

    #nav li {
        height: 70px;
    }

    #nav a {
        width: 50%;
        padding: 0;
    }

    #bar {
        height: auto;
        overflow: auto;
        width: 100%;
    }
}



